<template>
	<view class="content">
		<image class="img2" src="../../static/img2.png" mode="aspectFit"></image>
		<view class="title">设备物联孪生定位系统</view>
		<view class="wechatBox">
			<u-form :model="form" ref="uForm" label-width="200">
				<u-form-item label="设备名称">{{ form.jbxx1 }}</u-form-item>
				<u-form-item label="设备编号">{{ form.jbxx2 }}</u-form-item>
				<u-form-item label="记录时间">{{ now | date('yyyy年mm月dd日 hh时MM分') }}</u-form-item>
				<u-form-item label="提交人姓名"><u-input v-model="form.name" placeholder="请填写姓名" /></u-form-item>
				<u-form-item label="提交人科室"><u-input v-model="form.section" placeholder="请填写科室" /></u-form-item>
				<u-form-item label="当前所在位置" :border-bottom="false">{{ form.jbxx6 }}</u-form-item>
			</u-form>
			<view class="u-m-t-50"><u-button type="primary" ripple shape="circle" block @click="scan">扫一扫 患者住院码</u-button></view>
		</view>

		<u-popup mode="bottom" v-model="overModal" :mask-close-able="false">
			<view class="overContent">
				<image style="width: 200upx;height: 200upx;margin-bottom: 30upx;" src="/static/success.png" mode="aspectFit"></image>
				<view>登记成功</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
export default {
	data() {
		return {
			id: '',
			form: {},
			now: '',
			overModal: false
		};
	},
	onLoad(options) {
		this.id = options.id;
		this.getDetail();
		var myDate = new Date();
		this.now = myDate.getTime();
	},
	methods: {
		getDetail() {
			wx.request({
				url: 'https://sbzhgl.ifufan.com:4000/devices/' + this.id,
				method: 'GET',
				data: {},
				success: res => {
					if (res.data.code == 200) {
						this.form = res.data.data[0].info.detail;
						if (uni.getStorageSync('name')) {
							this.form.name = uni.getStorageSync('name');
							console.log(this.form.name);
						}
						if (uni.getStorageSync('section')) {
							this.form.section = uni.getStorageSync('section');
						}
					}
				}
			});
		},

		scan() {
			var that = this
			uni.scanCode({
				scanType: 'qrCode',
				success: function(res) {
					console.log(res.result);
					wx.request({
						url: 'https://api.ifufan.com:8800/cdr/getPatientBypi?zypi=' + res.result,
						method: 'GET',
						success: result => {
							that.form.zhuyuanhao = res.result;
							that.form.huanzhexingming = result.data['姓名'];
							that.form.bingqu = result.data['病区'];
							that.form.chuanghao = result.data['床号'];
							that.over();
						}
					});
				}
			});
		},

		over() {
			if (!this.form.name) {
				uni.showToast({
					title: '请填写提交人姓名',
					icon: 'none'
				});
				return false;
			}
			if (!this.form.section) {
				uni.showToast({
					title: '请填写提交人科室',
					icon: 'none'
				});
				return false;
			}
			if (!this.form.zhuyuanhao) {
				uni.showToast({
					title: '扫患者码失败,请重新扫码',
					icon: 'none'
				});
				return false;
			}
			let data = {
				jbxx1: this.form.jbxx1,
				jbxx2: this.form.jbxx2,
				jbxx3: this.now,
				jbxx4: this.form.name,
				jbxx5: this.form.section,
				jbxx6: this.form.section,
				jbxx7: this.form.zhuyuanhao,
				jbxx8: this.form.huanzhexingming,
				jbxx9: this.form.bingqu,
				jbxx10: this.form.chuanghao
			};
			wx.request({
				url: 'https://sbzhgl.ifufan.com:4000/devices',
				method: 'POST',
				data: {
					patientno: this.id,
					detail: data
				},
				success: res => {
					console.log(res.data);
					if (res.data.code == 200) {
						uni.setStorageSync('name', this.form.name);
						uni.setStorageSync('section', this.form.section);
						this.overModal = true;
						setTimeout(() => {
							uni.reLaunch({
								url: '/pages/index/home'
							});
						}, 2000);
					}
				}
			});
		}
	}
};
</script>

<style>
.content {
	padding: 40upx;
}
.wechatBox {
	padding: 40upx;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	border-radius: 40upx;
	background: rgba(255, 255, 255, 0.9);
	margin-top: 50upx;
	margin-bottom: 40upx;
	position: relative;
}

.title {
	font-size: 46rpx;
	font-weight: 700;
	color: #fff;
	text-align: center;
	z-index: 100;
	margin-top: -100upx;
}

.quit {
	position: fixed;
	margin: 20upx;
	left: 0;
	bottom: 0;
	width: 710upx;
}
.img2 {
	width: 300upx;
	margin: -100upx 175upx 0;
}

.overContent {
	text-align: center;
	font-size: 40upx;
	font-weight: 700;
	padding: 60upx 0;
}
</style>
